*{
    font-family: sans-serif;
}

#NameMain{
    width: 100%;
    height: 110vh;
    background: linear-gradient(to right,rgba(245,245,245,.8),rgba(245,245,245,.8)),url("https://d33wubrfki0l68.cloudfront.net/daac8858fd4a0ccea44f59dfd079c3c16c263f33/c157c/assets/svg/common-bg.svg");
    background-position: center;
    display: flex;
}

#Social{
    flex-direction: column;
    width: 4.5%;
    align-items: center;
    justify-content: center;
    background: white;
    height: 25%;
    gap: 10%;
    margin-top: 15%;
    /* border: 2px solid red; */


}

#Social>a{
width: 55%;
cursor: pointer;
transition: 0.5s;
}

#Social>a:hover{
    background-color: #f7e698;
    padding: 5%;
    border-radius:5px;
}


#nameDetails{
    /* border: 2px solid red; */
    height: fit-content;
    width: 100%;
    text-align: left;
    margin: auto;
    margin-top: 2%;
    margin-left: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

#nameDetails>div>h1{
font-size: 350%;
margin-bottom: 4%;
color: black;
}

#nameDetails>div>p{
    font-size: 22px;
    color: #666;
    width: 80%;
    /* margin: auto; */
    margin-bottom: 4%;
}

#nameDetails>div>a>button{
    color: #333333;
   padding: 3% 10% ;
   background-color: #facf0f; 
   border-radius: 5px;
   box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
   transition: 1s;
   font-size: 20px;
   letter-spacing: 2px;
   font-weight: 900;
   position: static;
   width: 30%;
}

#nameDetails>div>a>button:hover{
    letter-spacing: 5px;
}

#mouseIcon{
    /* border: 2px solid red; */
   flex-direction: column;
    margin: auto;
    width: 5%;
    margin-top: -5%;
    margin-left: 47%;
    justify-content: center;
    align-items: center;
    font-size: 1px;
    gap: 5px;
}

#mouseIcon>img{
    /* margin-top: 100%; */
    width: 70%;
}

#nameDetails>img{
    width: 40%;
}