*{
    font-family:  sans-serif;
}

#logo{
    width:6%;
    border-radius: 50px;
    border: 1px solid black;
    
}

#navbar_main{
    /* border: 2px solid red; */
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    justify-content: space-between;
    align-items: center;
    padding: 1.5%  2%  1.5% 4%;
    position: sticky;
    top: 0;
    background-color: white;
}

#nav1{
    align-items: center;
    gap: 2%;
    /* border: 2px solid red; */
    
}

#nav1>h1{
    font-size: 110%;
    font-weight: bold;
    color: black;
    transition: 1s;
    cursor: pointer;
}

#nav1>h1:hover{
    color: #facf0f;
}

#nav2{
    gap:10%;
    width:100%;
    font-size: 110%;
    /* border: 2px solid red; */
    color: black;
}

#nav2>a>button{
    /* color: red; */
    background-color: white;
    transition: 1s;
}

#nav2>a>:hover{
    color: #facf0f;
}

