*{
    font-family: sans-serif;
}

#about1{
    /* border: 2px solid red; */
    padding: 8% 0;
    background-color: #fafafa;
}

#about1>h2{
    letter-spacing: 5px;
    margin-bottom: 2%;
    color: black;

}

#about1>p{
    font-size: 120%;
    width: 60%;
    margin: auto;
    margin-top: 4%;
    color: #666;
}
#emptySpan{
    content: ' ';
    position: static;
    font-size: 25px;
    height: 0px;
    width: 2rem;
    color: #facf0f;
    font-weight: bolder;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
}


#about2{
    /* border: 2px solid red; */
    width: 50%;
    text-align: left;
    padding-left: 5%;
    background-color: #fafafa;
    padding-bottom: 10%;
}

#about2>p{
    width: 85%;
    word-spacing: 2px;
    color: #666;
}

#about2>p>a{
  color: #facf0f;
  font-weight: bold;
  text-decoration: underline;
}

#about2>h2{
    margin-bottom: 5%;
    color: black;
}

