#about2>a>button{
    background-color: #facf0f;
    letter-spacing: 2px;
    margin-top: 5%;
    padding: 4% 5%;
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    position: static;
    transition: 1s;
    width: 25%;
 }

 #about2>a>button:hover{
   letter-spacing: 5px;
 }

#skills{
    /* border: 2px solid red; */
    width: 50%;
    background-color: #fafafa;
    text-align: left;
    padding-right: 5%;
 
}


#skills>div{
 /* border: 2px solid red; */
width: 100%;
}

#skills>div>h2{
    margin-bottom: 4%;
    margin-left: 2%;
    color: black;
}

#skills>div>div>button{
    margin: 1%;
    font-weight: 900;
    background-color: #E7E7E7;
    color: #666;
    padding: 3% 6%;
    position: static;
    transition: 0.5s;
}

#skills>div>div>button:hover{
    color: #E7E7E7;
    background-color: #666;
}
