#emptySpan{
    content: ' ';
    position: static;
    height: 0px;
    width: 2rem;
    color: #facf0f;
    font-weight: bolder;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
}

#ProjectMain{
    /* border: 2px solid red; */
    padding: 8% 8% 0% 8%;
}

#projectbox1>h2{
    margin-bottom: 2%;
    color: black;

}

#projectbox1>p{
    width: 60%;
    font-size: 110%;
    color: #666;
    margin: auto;
    margin-top: 5%;
}

#projectbox1{
    margin-bottom: 10%;
}

.projectContainer{
    padding-bottom: 15%;
}

.projectContainer>div:nth-child(1){
    /* border: 2px solid red; */
    
    width: 50%;
}

.projectContainer>div:nth-child(2){
 width: 50%;
 text-align: left;
 padding-left: 5%;
 display: flex;
 flex-direction: column;
 justify-content: center;
 gap: 10%;
 color: black;
 font-size: 25px;
}



.projectContainer>div:nth-child(2)>a>button{
    background-color: #facf0f;
    box-shadow: 0 5px 15px 0 rgb(0 0 0 / 15%);
    width: 25%;
    padding: 5% 0;
    letter-spacing: 2px;
    position: static;
    transition: 1s;
}

.projectContainer>div:nth-child(2)>a>button:hover{
    letter-spacing: 5px;
    /* color: #fff; */
}

.websitePhoto{
    width:75%;
    height:fit-content;
    margin: -54.5% 0 0 12.7%;
    border-radius: 1px;
}