#ContactMain{
    background: linear-gradient(to right,rgba(245,245,245,.8),rgba(245,245,245,.8)),url("https://d33wubrfki0l68.cloudfront.net/daac8858fd4a0ccea44f59dfd079c3c16c263f33/c157c/assets/svg/common-bg.svg");
    padding: 8%;
}

#emptySpan{
    content: ' ';
    position: static;
    height: 0px;
    width: 2rem;
    color: #facf0f;
    font-weight: bolder;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px;
    font-size: 25px;
}

#ContactMain>div:nth-child(1){

 width: 60%;
 margin: auto;
 margin-bottom: 8%;
}

#ContactMain>div:nth-child(1)>h2{
    margin-bottom: 4%;
}

#ContactMain>div:nth-child(1)>p{
   margin-top: 8%;
}

#ContactMain>div:nth-child(2){
border-radius: 5px;
box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
margin: 0 18%;
padding: 3%;
background-color: white;


}

#ContactMain>div:nth-child(2)>div{
    position: static;
}

#ContactMain>div:nth-child(2)>div>label{
    margin: 5% 0 1% 0;
    font-weight: 700;
    color: #616161;
}

#ContactMain>div:nth-child(2)>div>input{
    padding: 5% ;
    background-color: #F0F0F0;
    font-weight: 700;
     position: static;
  
}

#ContactMain>div:nth-child(2)>div>input::placeholder{
    color: #999999;
    font-weight: 700;
}

#ContactMain>div:nth-child(2)>div>textarea{
    resize: none;
    margin-bottom: 5%;
    background-color: #F0F0F0;
    padding: 5%;
    font-weight: 700;
    position: static;
}

#ContactMain>div:nth-child(2)>div>textarea::placeholder{
    color: #999999;
    font-weight: 700;
}


#submitForm{
    /* border: 2px solid red; */
    width: 25%;
    border-radius: 5px;
    text-align: center;
    background-color: #facf0f;
    letter-spacing: 2px;
    color: black;
    margin: 0 0 0 75%;
    transition: 1s;
}


#submitForm:hover{
    letter-spacing: 6px;
}